import { useState } from 'react';
import './App.css';
import Todolist from './dolist';


function Start(){
  return(
    <>
    <div className="gotrip" onClick={ e => {
      document.querySelector(".air").classList.add("active");
      e.target.classList.add("active");
      document.querySelector(".navibutton").classList.add("on");
      document.querySelector(".container").classList.add("active");
      
      setTimeout(()=> document.querySelector(".air").classList.remove("active"),2900);
      }
    }>여행시작</div>
    <div className="air"><img width="300px" src="https://image.flaticon.com/icons/png/512/4812/4812653.png"></img></div>
    </>
  )
}

function airplane(){
  
}


function openNav(){
  document.querySelector(".leftnavi").style.width = "160px";
  document.querySelector(".container.active").style.display="none" ;
  
}

function closeNav() {
  document.querySelector(".leftnavi").style.width = "0";
  document.querySelector(".container.active").style.display = "block";
}


function Container(props){
  return(
  <div className="container">
    {props.children}
  </div>
  )
}

function LeftNavigation(props){
  let [navimode, setmode] = useState("closed")
  return(
    <nav className="leftnavi">
      <div className="navibutton" onClick={(e)=>{
      if(navimode==="closed"){
        openNav();
        e.target.style.backgroundImage = "url('https://image.flaticon.com/icons/png/512/561/561189.png')"
        setmode("open")
      } else if(navimode==="open"){
        closeNav();
        e.target.style.backgroundImage = "url('https://image.flaticon.com/icons/png/512/660/660376.png')"
        setmode("closed")
      }}}></div>
    <a href="#" onClick={()=> {
      window.scrollTo(0, 0);
      closeNav();
      document.querySelector(".navibutton").style.backgroundImage = "url('https://image.flaticon.com/icons/png/512/660/660376.png')"
      setmode("closed");
    }}>첫째날</a>
    <a href="#2" onClick={()=> {
      closeNav();
      document.querySelector(".navibutton").style.backgroundImage = "url('https://image.flaticon.com/icons/png/512/660/660376.png')"
      setmode("closed");
    }}>둘째서쪽</a>
    <a href="#3" onClick={()=> {
      closeNav();
      document.querySelector(".navibutton").style.backgroundImage = "url('https://image.flaticon.com/icons/png/512/660/660376.png')"
      setmode("closed");
    }}>둘째서귀포</a>
    </nav>
    
  )
} 

function App() {
  return (
    <div className="App">
      <Start></Start>
      <LeftNavigation></LeftNavigation>
      <Container>
      <Todolist></Todolist>
      </Container> 
    </div>
  );
}

export default App;
